.search {
  width: 232px;
  position: relative;
  cursor: text;

  &__form {
    display: flex; }

  &__field {
    position: relative;
    width: 100%; }

  .input {
    display: block;
    padding-left: 10px;
    padding-right: 20px;
    width: 100%;
    height: 34px;
    line-height: 34px;
    font-size: 15px;
    border: 1px solid $color-border;
    border-right: none;
    background-color: #ffffff;
    cursor: text;

    &::placeholder {
      color: $input-color; } }

  .btn {
    padding: 0;
    display: inline-block;
    width: 34px;
    height: 34px;
    line-height: 34px;
    color: #ffffff;
    background-color: $bg-green;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: $bg-green-light; } }

  .icon {
    top: -2px;
    font-size: 16px; }

  &.is-big {
    width: 100%;

    .search__icon {
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translateY(-50%); }

    .input {
      padding-left: 45px;
      font-size: 16px;
      height: 50px;
      line-height: 50px;
      color: #424242;

      &::placeholder {
        color: $input-color; } }

    .btn {
      display: inline-block;
      width: 150px;
      height: 50px;
      line-height: 50px;
      font-size: 17px;
      font-weight: 500;
      color: #ffffff;
      //+m($mobile)
      //  font-size: 14px
      //  height: 35px
      //  line-height: 35px
      //
      //+m($mobile)
 } }      //  width: 70px

  &.is-mobile {
    display: none;
    position: fixed;
    padding: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    transform: translateY(0);
    transform-origin: top right;
    transition: all 0.25s ease-in-out;
    z-index: 20;

    @include m($mobile) {
      display: block; }

    .input {
      padding-left: 20px;
      height: 40px;
      line-height: 40px; }

    .btn {
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center; }

    &.is-open {
      transform: translateY(100%);
      .search__form {
        -webkit-box-shadow: 1px 1px 4px 2px rgba(224,224,224,1);
        -moz-box-shadow: 1px 1px 4px 2px rgba(224,224,224,1);
        box-shadow: 1px 1px 4px 2px rgba(224,224,224,1); } } } }
