.error {
  position: relative;
  height: 100%;

  &__wrap {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center; }

  &__name {
    font-size: 104px;
    font-weight: 500;

    @include m($mobile) {
      font-size: 80px; }

    @include m(480) {
      font-size: 50px; } }

  &__descr {
    margin-bottom: 20px;
    font-size: 18px;
    text-align: center;

    @include m($mobile) {
      font-size: 14px; } }

  .btn-green {
    @include m($mobile) {
      width: 100px;
      height: 30px;
      line-height: 30px; } } }
