.sorting {
  display: flex;
  align-items: center;
  font-size: 13px;

  @include m($mobile) {
    width: 100%;
    flex-direction: column;
    align-items: baseline; }

  span {
    @include m($mobile) {
      margin-bottom: 5px; } }

  &__list {
    margin-left: 10px;
    display: flex;
    @include m($mobile) {
      width: 100%;
      margin-left: 0; } }

  &__el {
    padding: 0 16px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    border: 1px solid $color-border;
    transition: all .3s ease-in-out;
    @include m($mobile) {
      width: 33%;
      padding: 0; }

    a {
      color: $text; }

    &:nth-child(2) {
      border-left: none;
      border-right: none; }

    &.is-active {
      background-color: $bg-green;
      border: 1px solid $bg-green;

      a {
        color: #ffffff; }

      &:nth-child(2) {
        border-left: none;
        border-right: none; } } } }
