.btn-download {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 46px;
  line-height: 46px;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  background-color: $bg-green;
  cursor: pointer;
  transition: all .3s ease-in-out;

  &:hover {
    background-color: $bg-green-light; }

  &.googleplay {
    background-color: #000000;

    &:hover {
      background-color: #343434; } }

  &.is-transparent {
    border: 1px solid $bg-green;
    color: $bg-green;
    background-color: #fff;

    .bold {
      margin-right: 0; } }

  .icon {
    margin-right: 10px; }

  .bold {
    margin-right: 5px; } }

.btn-transparent {
  display: inline-block;
  font-size: 13px;
  width: 150px;
  height: 34px;
  line-height: 33px;
  color: $link;
  text-align: center;
  border: 1px solid $color-border;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  cursor: pointer;

  &:hover {
    background-color: #fafafa; } }

.search-btn {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: #ffffff;
  text-align: center;
  background-color: $bg-green-light;
  cursor: pointer; }

.btn-green {
  display: inline-block;
  font-size: 14px;
  width: 150px;
  height: 34px;
  line-height: 33px;
  color: #ffffff;
  text-align: center;
  background-color: $bg-green;
  transition: all 0.3s ease-in-out;
  cursor: pointer;

  &:hover {
    background-color: $bg-green-light; } }
