.block-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;

  @include m($mobile) {
    margin-bottom: 10px;
    flex-wrap: wrap; }

  &__title {
    margin-bottom: 9px;
    font-size: 20px;
    font-weight: 500;
    color: $text; }

  &__more {
    color: $link; }

  &__wrap {
    display: flex;
    align-items: center; }

  &__right {
    position: relative;
    left: -6px;

    @include m($mobile) {
      left: 0; } }

  &__icon {
    display: flex;
    font-size: 26px;
    margin-right: 8px; }

  &.with-border {
    border-bottom: 1px solid $color-border; } }
