@include font('apksitory-icons', 'apksitory-icons');

html,
body {
  width: 100%;
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 1.4;
  color: $text;
  font-family: $font-main;
  font-weight: 400; }

a {
  display: inline-block;
  text-decoration: none;
  color: $link;
  transition: all .3s ease-in-out; }

img {
  display: inline-block;
  max-width: 100%; }

input:focus,
select:focus,
button:focus {
  outline: none; }

input[type='text'],
input[type='search'],
input[type='button'] {
  -moz-appearance: none;
  -webkit-appearance: none; }

.copyright {
  margin-right: 2px;
  font-size: 13px;
  color: $text; }

.changelog {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid $color-border; }

.raiting {

  &__total {
    margin-top: 5px;
    display: block;
    color: $link; } }

.results {
  padding-bottom: 5px;
  font-size: 15px;
  color: $text-light;
  border-bottom: 1px solid $color-border; }

.preview {
  margin-bottom: 26px; }

.banner {
  margin-bottom: 15px; }

.bold {
  font-weight: 500; }

.green {
  color: #66bb6a; }

.rating-box {
  flex: 0 0 220px;
  width: 220px;
  border: 1px solid #dddddd;
  text-align: center;
  display: flex;
  flex-direction: column;
  @include m($mobile) {
    display: none; }

  &__name {
    height: 46px;
    line-height: 46px;
    text-align: center;
    background-color: #f9f9f9;
    border-bottom: 1px solid #dddddd;
    margin: 0; }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    margin: 0; }


